import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import {
  Calendar,
  Users,
  FileText,
  Brain,
  Clock,
  Search,
  Plus,
  Bell,
  Settings,
  Stethoscope,
  Activity,
  TrendingUp,
  AlertTriangle,
  CheckCircle2,
  Eye,
  Edit,
  Send,
  Phone,
  Video,
  MessageSquare,
  Download,
  Upload,
  BarChart3,
  Heart,
  Thermometer,
  Clipboard,
  Star,
  Filter,
} from "lucide-react"

export default function DoctorDashboard() {
  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50/30 to-green-50/30">
      {/* Header */}
      <div className="border-b bg-white/90 backdrop-blur-sm sticky top-0 z-40">
        <div className="container py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Avatar className="h-10 w-10">
                <AvatarImage src="/placeholder-user.jpg" />
                <AvatarFallback>DS</AvatarFallback>
              </Avatar>
              <div>
                <h1 className="text-xl font-semibold">Dr. Sarah Smith</h1>
                <p className="text-sm text-muted-foreground">Cardiologist • City Hospital</p>
              </div>
            </div>
            <div className="flex items-center gap-2">
              <Button variant="ghost" size="sm">
                <Bell className="h-4 w-4" />
                <Badge className="ml-1 h-4 w-4 rounded-full p-0 text-xs">5</Badge>
              </Button>
              <Button variant="ghost" size="sm">
                <Settings className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </div>
      </div>

      <div className="container py-8">
        {/* Quick Stats */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
          <Card className="card-hover">
            <CardContent className="p-4">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-primary/10 rounded-lg">
                  <Calendar className="h-5 w-5 text-primary" />
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Today's Appointments</p>
                  <p className="text-2xl font-bold">12</p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card className="card-hover">
            <CardContent className="p-4">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-secondary/10 rounded-lg">
                  <Users className="h-5 w-5 text-secondary" />
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Total Patients</p>
                  <p className="text-2xl font-bold">247</p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card className="card-hover">
            <CardContent className="p-4">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-accent/10 rounded-lg">
                  <FileText className="h-5 w-5 text-accent" />
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Pending Reviews</p>
                  <p className="text-2xl font-bold">8</p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card className="card-hover">
            <CardContent className="p-4">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-green-100 rounded-lg">
                  <TrendingUp className="h-5 w-5 text-green-600" />
                </div>
                <div>
                  <p className="text-sm text-muted-foreground">Success Rate</p>
                  <p className="text-2xl font-bold text-green-600">94%</p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        <div className="grid lg:grid-cols-3 gap-6">
          {/* Left Column */}
          <div className="lg:col-span-2 space-y-6">
            {/* Today's Schedule */}
            <Card>
              <CardHeader className="flex flex-row items-center justify-between">
                <div>
                  <CardTitle>Today's Schedule</CardTitle>
                  <CardDescription>November 23, 2024 • 8 appointments remaining</CardDescription>
                </div>
                <Button size="sm">
                  <Plus className="h-4 w-4 mr-1" />
                  Add Slot
                </Button>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {[
                    {
                      time: "9:00 AM",
                      patient: "John Martinez",
                      type: "Follow-up",
                      status: "completed",
                      condition: "Hypertension",
                      urgent: false,
                    },
                    {
                      time: "9:30 AM",
                      patient: "Sarah Johnson",
                      type: "Consultation",
                      status: "in-progress",
                      condition: "Chest Pain",
                      urgent: true,
                    },
                    {
                      time: "10:00 AM",
                      patient: "Robert Chen",
                      type: "Checkup",
                      status: "waiting",
                      condition: "Regular Checkup",
                      urgent: false,
                    },
                    {
                      time: "10:30 AM",
                      patient: "Maria Garcia",
                      type: "Follow-up",
                      status: "scheduled",
                      condition: "Arrhythmia",
                      urgent: false,
                    },
                  ].map((appointment, index) => (
                    <div key={index} className="flex items-center justify-between p-4 border rounded-lg card-hover">
                      <div className="flex items-center gap-4">
                        <div className="text-center">
                          <p className="text-sm font-medium">{appointment.time}</p>
                          <Badge
                            variant={
                              appointment.status === 'completed' ? 'default' :
                              appointment.status === 'in-progress' ? 'secondary' :
                              appointment.status === 'waiting' ? 'outline' : 'secondary'
                            }
                            className="text-xs mt-1"
                          >
                            {appointment.status}
                          </Badge>
                        </div>
                        <div className="flex-1">
                          <div className="flex items-center gap-2">
                            <p className="font-medium">{appointment.patient}</p>
                            {appointment.urgent && (
                              <AlertTriangle className="h-4 w-4 text-red-500" />
                            )}
                          </div>
                          <p className="text-sm text-muted-foreground">{appointment.condition}</p>
                          <Badge variant="outline" className="text-xs mt-1">
                            {appointment.type}
                          </Badge>
                        </div>
                      </div>
                      <div className="flex gap-2">
                        <Button variant="ghost" size="sm">
                          <Eye className="h-4 w-4" />
                        </Button>
                        <Button variant="ghost" size="sm">
                          <MessageSquare className="h-4 w-4" />
                        </Button>
                        {appointment.status === 'waiting' && (
                          <Button size="sm">Start</Button>
                        )}
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* Patient Search & Management */}
            <Card>
              <CardHeader>
                <CardTitle>Patient Management</CardTitle>
                <CardDescription>Search and manage your patients</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex gap-2 mb-4">
                  <div className="flex-1 relative">
                    <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                    <Input className="pl-9" placeholder="Search patients..." />
                  </div>
                  <Button variant="outline">
                    <Filter className="h-4 w-4" />
                  </Button>
                </div>

                <Tabs defaultValue="recent" className="w-full">
                  <TabsList className="grid w-full grid-cols-3">
                    <TabsTrigger value="recent">Recent</TabsTrigger>
                    <TabsTrigger value="critical">Critical</TabsTrigger>
                    <TabsTrigger value="all">All Patients</TabsTrigger>
                  </TabsList>

                  <TabsContent value="recent" className="space-y-3">
                    {[
                      {
                        name: "Emma Wilson",
                        age: 45,
                        lastVisit: "2 days ago",
                        condition: "Diabetes",
                        risk: "medium",
                        avatar: "EW"
                      },
                      {
                        name: "David Brown",
                        age: 62,
                        lastVisit: "1 week ago",
                        condition: "Heart Disease",
                        risk: "high",
                        avatar: "DB"
                      },
                      {
                        name: "Lisa Davis",
                        age: 38,
                        lastVisit: "3 days ago",
                        condition: "Hypertension",
                        risk: "low",
                        avatar: "LD"
                      },
                    ].map((patient, index) => (
                      <div key={index} className="flex items-center justify-between p-3 border rounded-lg card-hover cursor-pointer">
                        <div className="flex items-center gap-3">
                          <Avatar className="h-10 w-10">
                            <AvatarFallback>{patient.avatar}</AvatarFallback>
                          </Avatar>
                          <div>
                            <p className="font-medium text-sm">{patient.name}</p>
                            <p className="text-xs text-muted-foreground">
                              {patient.age} years • Last visit: {patient.lastVisit}
                            </p>
                            <div className="flex items-center gap-2 mt-1">
                              <Badge variant="outline" className="text-xs">{patient.condition}</Badge>
                              <Badge 
                                variant={patient.risk === 'high' ? 'destructive' : patient.risk === 'medium' ? 'secondary' : 'default'}
                                className="text-xs"
                              >
                                {patient.risk} risk
                              </Badge>
                            </div>
                          </div>
                        </div>
                        <div className="flex gap-1">
                          <Button variant="ghost" size="sm">
                            <Eye className="h-4 w-4" />
                          </Button>
                          <Button variant="ghost" size="sm">
                            <Phone className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    ))}
                  </TabsContent>

                  <TabsContent value="critical">
                    <div className="space-y-3">
                      <div className="p-4 border-l-4 border-red-500 bg-red-50/50 rounded-lg">
                        <div className="flex items-center justify-between">
                          <div>
                            <p className="font-medium text-red-900">Michael Thompson</p>
                            <p className="text-sm text-red-700">Severe chest pain - Requires immediate attention</p>
                          </div>
                          <Button size="sm" className="bg-red-600 hover:bg-red-700">
                            <Phone className="h-4 w-4 mr-1" />
                            Call Now
                          </Button>
                        </div>
                      </div>
                    </div>
                  </TabsContent>

                  <TabsContent value="all">
                    <div className="text-center py-8">
                      <Users className="h-12 w-12 mx-auto text-muted-foreground mb-4" />
                      <p className="text-muted-foreground">View all 247 patients</p>
                      <Button variant="outline" className="mt-2">
                        Load All Patients
                      </Button>
                    </div>
                  </TabsContent>
                </Tabs>
              </CardContent>
            </Card>

            {/* E-Prescription */}
            <Card>
              <CardHeader>
                <CardTitle>Create E-Prescription</CardTitle>
                <CardDescription>Digital prescription for current patient</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="text-sm font-medium">Patient</label>
                      <Input placeholder="Select patient..." value="Sarah Johnson" readOnly />
                    </div>
                    <div>
                      <label className="text-sm font-medium">Date</label>
                      <Input type="date" value="2024-11-23" />
                    </div>
                  </div>
                  
                  <div>
                    <label className="text-sm font-medium">Medications</label>
                    <div className="space-y-2 mt-2">
                      <div className="flex items-center gap-2 p-2 border rounded">
                        <Input placeholder="Medication name" className="flex-1" />
                        <Input placeholder="Dosage" className="w-24" />
                        <Input placeholder="Frequency" className="w-24" />
                        <Button variant="ghost" size="sm">×</Button>
                      </div>
                      <Button variant="outline" size="sm">
                        <Plus className="h-4 w-4 mr-1" />
                        Add Medication
                      </Button>
                    </div>
                  </div>
                  
                  <div>
                    <label className="text-sm font-medium">Instructions</label>
                    <Textarea placeholder="Special instructions for the patient..." />
                  </div>
                  
                  <div className="flex gap-2">
                    <Button>
                      <Send className="h-4 w-4 mr-1" />
                      Send Prescription
                    </Button>
                    <Button variant="outline">Save Draft</Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Right Column */}
          <div className="space-y-6">
            {/* AI Medical Insights */}
            <Card className="border-primary/20 bg-gradient-to-br from-primary/5 to-secondary/5">
              <CardHeader>
                <div className="flex items-center gap-2">
                  <Brain className="h-5 w-5 text-primary" />
                  <CardTitle>AI Medical Insights</CardTitle>
                </div>
                <CardDescription>AI-powered patient analysis</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="p-3 bg-white/50 rounded-lg">
                  <div className="flex items-start gap-2">
                    <AlertTriangle className="h-4 w-4 text-orange-500 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Risk Alert</p>
                      <p className="text-xs text-muted-foreground">3 patients show elevated cardiac markers</p>
                    </div>
                  </div>
                </div>
                <div className="p-3 bg-white/50 rounded-lg">
                  <div className="flex items-start gap-2">
                    <TrendingUp className="h-4 w-4 text-green-500 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Treatment Success</p>
                      <p className="text-xs text-muted-foreground">85% improvement in diabetes patients</p>
                    </div>
                  </div>
                </div>
                <div className="p-3 bg-white/50 rounded-lg">
                  <div className="flex items-start gap-2">
                    <Clock className="h-4 w-4 text-blue-500 mt-0.5" />
                    <div>
                      <p className="text-sm font-medium">Follow-up Due</p>
                      <p className="text-xs text-muted-foreground">12 patients need check-ups this week</p>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Patient Summary */}
            <Card>
              <CardHeader>
                <CardTitle>Current Patient Summary</CardTitle>
                <CardDescription>Sarah Johnson • 32 years old</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div className="p-3 bg-muted/30 rounded-lg">
                      <div className="flex items-center gap-2 mb-1">
                        <Heart className="h-4 w-4 text-red-500" />
                        <span className="text-sm font-medium">BP</span>
                      </div>
                      <p className="text-lg font-bold">140/90</p>
                      <p className="text-xs text-orange-600">Elevated</p>
                    </div>
                    <div className="p-3 bg-muted/30 rounded-lg">
                      <div className="flex items-center gap-2 mb-1">
                        <Activity className="h-4 w-4 text-blue-500" />
                        <span className="text-sm font-medium">HR</span>
                      </div>
                      <p className="text-lg font-bold">88 bpm</p>
                      <p className="text-xs text-green-600">Normal</p>
                    </div>
                  </div>
                  
                  <div>
                    <h4 className="font-medium text-sm mb-2">Recent Symptoms</h4>
                    <div className="space-y-1">
                      <Badge variant="outline" className="text-xs">Chest pain</Badge>
                      <Badge variant="outline" className="text-xs">Shortness of breath</Badge>
                      <Badge variant="outline" className="text-xs">Fatigue</Badge>
                    </div>
                  </div>
                  
                  <div>
                    <h4 className="font-medium text-sm mb-2">Current Medications</h4>
                    <div className="text-sm space-y-1">
                      <p>• Lisinopril 10mg daily</p>
                      <p>• Metoprolol 25mg twice daily</p>
                    </div>
                  </div>
                  
                  <div className="flex gap-2">
                    <Button variant="outline" size="sm" className="flex-1">
                      <FileText className="h-4 w-4 mr-1" />
                      Full History
                    </Button>
                    <Button variant="outline" size="sm" className="flex-1">
                      <Upload className="h-4 w-4 mr-1" />
                      Add Note
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Quick Actions */}
            <Card>
              <CardHeader>
                <CardTitle>Quick Actions</CardTitle>
              </CardHeader>
              <CardContent className="space-y-2">
                <Button variant="outline" size="sm" className="w-full justify-start">
                  <Video className="h-4 w-4 mr-2" />
                  Start Video Call
                </Button>
                <Button variant="outline" size="sm" className="w-full justify-start">
                  <FileText className="h-4 w-4 mr-2" />
                  Write Prescription
                </Button>
                <Button variant="outline" size="sm" className="w-full justify-start">
                  <Calendar className="h-4 w-4 mr-2" />
                  Schedule Follow-up
                </Button>
                <Button variant="outline" size="sm" className="w-full justify-start">
                  <Upload className="h-4 w-4 mr-2" />
                  Request Lab Tests
                </Button>
              </CardContent>
            </Card>

            {/* Performance Stats */}
            <Card>
              <CardHeader>
                <CardTitle>This Month's Performance</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex justify-between items-center">
                  <span className="text-sm">Patients Treated</span>
                  <span className="font-bold">89</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm">Success Rate</span>
                  <span className="font-bold text-green-600">94%</span>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm">Avg. Rating</span>
                  <div className="flex items-center gap-1">
                    <Star className="h-4 w-4 fill-yellow-400 text-yellow-400" />
                    <span className="font-bold">4.8</span>
                  </div>
                </div>
                <div className="flex justify-between items-center">
                  <span className="text-sm">Response Time</span>
                  <span className="font-bold">12 min</span>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}